.title {
    font-size: 23px;
    font-weight: 700;
}

.daily-item {
    background-color: rgb(196, 217, 92);
    border-radius: 15px;
    height: 40px;
    margin: 5px;
    display: flex;
    align-items: center;
    cursor: 14px;
    padding: 5px 20px;
}

.icon-small {
    width: 40px;
}

.day {
    color: black;
    flex: 1 1;
    font-weight: 600;
    margin-left: 15px;
}

.description {
    flex: 1 1;
    text-align: right;
    margin-right: 15px;
}
              
.min-max{
    color:#757575;

    
}
.daily-details-grid{
    grid-row-gap: 0;
    grid-column-gap: 15px;
    row-gap: 0;
    column-gap:15px;
    display: grid;
    flex:1 1;
    grid-template-columns: auto auto;
    padding: 15px 15px;
}
.daily-details-grid-item{
  display: flex;
  height:30px;
  justify-content: space-between;
  align-items: center;
}
.daily-details-grid-item label:first-child{
    color:#000000;
    font-weight: bold; /* Mettre en gras */
}
.daily-details-grid-item label:last-child{
    color: rgb(49, 45, 45);
}
.arrow-icon {
    font-size: 16px; 
    margin-left: 5px; 
    font-weight: bold; /* Mettre en gras */
  }